<template>
    <div class="container_wrap">
        <span :class="options.must?'have':'no'">
            <em>{{ options.title}}</em>
            <i>：</i>
        </span> 
        <div class="input_wrap">
            <input :type="options.type" :required="options.must"   ref="node_input" :placeholder="'请输入你的'+`${options.title}`" v-model="input_value"  :name="options.name" @blur="check($event)">
            <a href="javaScript:void(0);" v-if="options.RegExp_test" :class="rea_className.a">{{ attention_valid }}</a>
            <span v-if="options.type=='password'" class="unvisible" @click="changeVisible($event)"></span>
        </div>
        <slot name="indentify_code" :time="timing" :reqIndentify="throttle_reqIndentify" :title="title"></slot>

    </div>
</template>
<script setup>
import { isProxy, onBeforeMount, reactive, ref, watch } from 'vue';
    let props = defineProps(['options']) 
    let emit = defineEmits(['get_password'])
    let options = props.options
    let input_value = ref()
    let node_input = ref()
    let timing = ref(60)// (number)显示验证码有效时间。默认一分钟
    let title = ref('发送验证')//(string)
    let attention_valid = ref('')
    let rea_className = reactive({})


    //表单失焦的时候验证并传递值出去
    let check = () => {
        if(options.RegExp_test){
            let reg= options.RegExp
            let judge = reg.test(input_value.value)
            if(!judge){
                if(input_value.value!==''){
                    rea_className.a = 'attention'
                    attention_valid.value = `${options.title}` + `${options.RegExp_context}`
                }else{
                    if(options.must){
                        rea_className.a = 'attention'
                        attention_valid.value = `此项必须填写`  
                    }
                }
            }else{
                rea_className.a = ''       
            }
        }      
    }

    function changeVisible($event){
        node_input.value.type = $event.target.className=='visible'?'password':'text'
        $event.target.className = $event.target.className=='visible'?'unvisible':'visible'
    }
    //发送手机验证的请求，点击事件节流 1分钟，收到确认发送数据定时器一分钟显示有效
    let throttle = (
        fnc,//(function）节流函数
        wait //（number）等待时间
        ) => {
        let timer = null
    
        return (params) => {
            if(!timer){
                timer = setTimeout(() => {
                    clearTimeout(timer)
                    timer= null                
                },wait)
                fnc(params)
            }        
        }     
    }
    let reqIndentify = ()=>{
        let time_count = setInterval(() => {
            timing.value = timing.value -1  

            if(timing.value<=0){
            clearInterval(time_count)
            time_count = null
            title.value = '再次发送'
            
        }
        },1000)
  
    }
    
    let throttle_reqIndentify= throttle(reqIndentify,60000)





</script>

<style lang="scss">
// ./images/visible.jpg
// ./images/unvisible.jpg

    .container_wrap {
        margin-bottom: 16px;
        position: relative;


        span{
            text-align: right;
            font-size: 14px;
            float: left;
            margin-top: 5px;
            display: block;
            width: 70px;
            position: relative;
            &.have{
                &::before{
                content:'*';
                position:absolute;
                right: 0px;
                top: 0px;
                color: red;
                }
            }

        }
        .input_wrap{
            position: relative;
            float: left;
            a{
                position: absolute;
                display: none;
                font-size: 12px;
                text-decoration: none;
                &.attention{
                    display: block;
                    color: red;
                    content: attr(class);
                }

            }

            input{

                height: 32px;
                box-sizing: border-box;
                border: 1px solid #ccc;
                padding-top: 6px;
                padding-bottom: 6px;
                font-size: 14px;
                line-height: 22px;
                padding-right: 8px;
                padding-left: 8px;
                border-radius: 0 2px 2px 0;
            }


 

            
            span.visible{
                width: 20px;
                height: 20px;
                background-image: url(./images/visible.jpg);
                background-size: contain;
                background-position: 0px ;
                position: absolute;
                top: 0px;
                right: 0px;
                }
            span.unvisible{
                @extend .visible;
                background-image:url(./images/unvisible.jpg) ;
            }

        }
        .indentify_code_wrap{
           position:absolute;
           top: 0;
           right: -70px;
           height: 30px;
           

           .btn_wrap{
            float: left;
            height: 30px;
            margin-top: 10px;
            
            button{
                width: inherit;
               
            }

           }
           .timing_wrap{
            float: left;
            width: 40px;
            margin-top: 10px;
            
            span{
                width: 20px;
                float: none;
                display: inline;
                margin: 0 0;
            }
           }
           &::after{
            content: '';
            display: block;
            clear: both;
            overflow: hidden;
           }
        }
        &::after{
            content: '';
            display: block;
            clear: both;
            overflow: hidden;
        }
    }
</style>
