<template>

  <div v-if="true" class="register-container">
    <!-- 注册内容 -->
    <div class="register">
      <h3>注册新用户
        <span class="go">我有账号，去 <a href="login.html" target="_blank">登陆</a>
        </span>
      </h3>
      <form action="http://localhost:8080/proxy/register" ref="node_form" method="post">
        <div class="input_comonents">
          <Mini_input :options="input_options.account" ></Mini_input>
          <Mini_input :options="input_options.password" v-on:get_password="get_password"></Mini_input>
          <Mini_input :options="input_options.tel" ></Mini_input>
          <Mini_input :options="input_options.check_password" ></Mini_input>
          <Mini_input :options="input_options.email" ></Mini_input>
          <Mini_input :options="input_options.indentify" v-on:pub="havetry">
            <template #indentify_code="params">
              <div class="indentify_code_wrap">
                <div class="btn_wrap">
                  <button type="button" @click="params.reqIndentify()">{{ params.title }}</button>
                </div>
                <div class="timing_wrap">
                  <span>{{ params.time }}</span>
                  <em>s</em>
                </div>
             </div>
            </template>
          </Mini_input>
        </div>
        <div class="btn">
          <button type="button" @click="reqSubmit($event)">{{ title }}</button>
          <span>{{attention}}</span>
        </div>     
      </form>
    </div>

    <!-- 底部 -->
  </div>
  <div class="resolve-wrap" v-if="false">
    <div class="resolve-container">
      <div class="resolve">
        <img src="./images/success.png" alt="success" srcset="">
        <span>注册成功</span>
      </div>
      <div class="back-home">
        <button>进入登陆页面</button>
        <div class="autoOpen">
          <!-- <em>60s</em> -->
          <span>内自动跳转到首页</span>
        </div>
      </div>

    </div>
  </div>


</template>


<script>
import Mini_input from '@/ui-mini/mini_input.vue'
import { ref } from 'vue';
import { reactive } from 'vue';
  export default {
    name: 'Register',
    components:{Mini_input},
    setup(){
      let input_options = reactive({
         'account': {
            'type':'text',
            'name':'account',
            'title':'用户名',
            'RegExp_test':true,
            'RegExp':/^[a-zA-Z0-9]\w{5,16}$/,
            'RegExp_context':'必须为8-16位字母或数字下划线组成',
            'must':true,
          },
         'password':{
            'type':'password',
            'name':'password',
            'title':'密码',
            'RegExp_test':true,
            'RegExp':/^[a-zA-Z0-9]\w{5,16}$/,
            'RegExp_context':'必须为8-16位字母或数字下划线组成',
            'must':true,
         },
          'check_password':{
            'type':'password',
            'name':'check_password',
            'title':'确认密码',
            'RegExp_test':true,
            'RegExp':/^[a-zA-Z0-9]\w{5,16}$/,
            'RegExp_context':'必须为8-16位字母或数字下划线组成',
            'must':true,
         },
         'tel':{
            'type':'text',
            'name':'tel',
            'title':'手机号',
            'RegExp_test':true,
            'RegExp':/^[0-9]{8,13}$/,
            'RegExp_context':'输入不正确',
            'must':true,
         },
        'email': {
            'type':'text',
            'title':'邮箱',
            'name':'email',
            'RegExp_test':true,
            'RegExp':/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
            'RegExp_context':'输入不正确',
            'must':false,
         },
         'indentify':{
          'type':'text',
          'name':'indentify',
          'title':'验证码',
          'RegExp_test':false,
          'must':true

         }
        })
      
      let node_form = ref()
      let attention = ref('') //string 验证注册提示
      let title = ref('点击注册') //string注册按钮内容

      function havetry(params){
        console.log(params);
      }

      let get_password = (params) => {
        let form_data = new FormData(node_form.value)       
      }

      let pending_form = new WeakMap()

      let reqSubmit = ($event) => {
        let e = $event||window.event
        let form = node_form.value //element元素节点
        let form_data = new FormData(node_form.value)//form表单数据对象
        let previous_form= pending_form.get(form)
        const controller = new AbortController()


        //验证密码重复输入
        if(form_data.get('check_password')!==form_data.get('password')){
          attention.value = '确认密码输入不正确'
          return null

        }
        //确认必填项已经填入
        for(let value in input_options){
          if(input_options[value].must&&form_data.get(input_options[value].name)==''){
            attention.value ='请填入必填项'
            return null
          }
        }

        //通过外置flag：weakMap来防抖，取消上一个然后再发
        console.log(previous_form,Boolean(previous_form));
        if(previous_form){
          previous_form.abort()      
        }

        fetch(form.action,{
          method:form.method,
          body:form_data,
          signal:controller.signal
          }
        ).then(async (response) => {

          if(response.ok&&response.status==200){
            let res =  await response.clone().json()
            pending_form.delete(form)
            title.value='注册成功'

            //这里缺失判断注册成功

            return res
          }else{
            return Promise.reject(response)
          }
     
        })
        .catch((err) => { console.log(err)})

        pending_form.set(form,controller)

        title.value='注册中....'



      
        //阻止表单默认提交
        e.preventDefault()
        
      } 
      return{
        input_options,
        attention,
        havetry,
        get_password,
        node_form,
        reqSubmit,
        title,
      }

    }
  }
</script>

<style lang="less" scoped>
  .register-container {
    .register {
      width: 1200px;
      height: 445px;
      border: 1px solid rgb(223, 223, 223);
      margin: 0 auto;

      h3 {
        background: #ececec;
        margin: 0;
        padding: 6px 15px;
        color: #333;
        border-bottom: 1px solid #dfdfdf;
        font-size: 20.04px;
        line-height: 30.06px;

        span {
          font-size: 14px;
          float: right;

          a {
            color: #e1251b;
          }
        }
      }

      form{
         div.container_wrap{
          width: 400px;
          margin: 0 auto;
          margin-top: 20px;
          :deep input{
            width: 300px;
          }

        }
      }

      // .controls {
      //   text-align: center;
      //   position: relative;

      //   input {
      //     vertical-align: middle;
      //   }

      //   .error-msg {
      //     position: absolute;
      //     top: 100%;
      //     left: 495px;
      //     color: red;
      //   }
      // }

      .btn {
        text-align: center;
        line-height: 36px;
        margin: 17px 0 0 55px;

        button {
          outline: none;
          width: 270px;
          height: 36px;
          background: #e1251b;
          color: #fff !important;
          display: inline-block;
          font-size: 16px;
        }
        span{
          display: block;
          margin: 0 0 ;
          color: #e1251b;

        }
      }
    }

    // .copyright {
    //   width: 1200px;
    //   margin: 0 auto;
    //   text-align: center;
    //   line-height: 24px;

    //   ul {
    //     li {
    //       display: inline-block;
    //       border-right: 1px solid #e4e4e4;
    //       padding: 0 20px;
    //       margin: 15px 0;
    //     }
    //   }
    // }
  }
  .resolve-wrap{
    background-image: url(./images/滑雪.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 0 20px;
    width: 900px;
    height: 400px;
    margin: 0 auto;

    position: relative;
    .resolve-container{
      width: 300px;
      height: 100px;
    
      position: absolute;
      top: 0;
      bottom:0;
      left: 0;
      right: 0;
      margin: auto auto;
      .resolve{
        float: left;
        img{
          width: 70px;
          height: 70px;
          display: block;
        }
        span{
          margin-left: 10px;
          font-size: 14px;
          font-weight: bold;
        }

      }
      .back-home{
        float: left;
        margin-left: 30px;
        margin-top: 10px;
        
        
        button{
          width: 150px;
          height: 50px;
          font-size: 14px;
          font-weight: bold;
          border: solid 1px black;
          cursor: pointer;
          
          border-radius: 25px;
          &:hover{
            border-color: red;
          }
          &:active{
            opacity: 0;
          }
        }
        @property --t{
          syntax: '<integer>';
          inherits: false;
          initial-value: 0;
        }
        @keyframes count {
          to{
            --t:0;
          }
          
        }
        div.autoOpen{
          margin-left: 15px;
          margin-top: 5px;
          &::before{
            --t:60;
            counter-increment: digit var(--t);
            content: counter(digit);
            animation: count 60s forwards;
          }

        }
          
      }
    }
  }
</style>